// src/redux/reducers/blockSequenceReducer.js

import { SET_BLOCK_SEQUENCE_START, SET_BLOCK_SEQUENCE_END } from "../actions/blockSequenceActions";

const initialState = {
  blockSequenceStart: "",
  blockSequenceEnd: "",
};

const blockSequenceReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_BLOCK_SEQUENCE_START:
      return {
        ...state,
        blockSequenceStart: action.payload,
        
      };
    case SET_BLOCK_SEQUENCE_END:
      return {
        ...state,
        blockSequenceEnd: action.payload,
      };
    default:
      return state;
  }
};

export default blockSequenceReducer;
