import { createSlice } from "@reduxjs/toolkit";

export const basicSlice = createSlice({
  name: "basic",
  initialState: {
    residentTypes: [],
    propertyTypes: [],
    showExtendedColumns: false,
    propertyId: "",
    propertyName: "",
    propertyStatus: "",
    TotalDraft: false,
    basicDetails: null,
    showPendingProperty: false,
    tabStatus: false,
    tabDirty: false,
    unitCreationDraft: "",
    propertyActionStatus: "",
  },
  reducers: {
    updateResidentTypes: (state = basicSlice?.initialState, action) => {
      const data = action.payload?.data || [];
      const residentTypes = data.map((o) => ({
        value: o._id,
        label: o.residentType,
        ...o,
      }));
      state.residentTypes = [...residentTypes];
    },
    updatePropertyTypes: (state = basicSlice?.initialState, action) => {
      const data = action.payload?.data || [];
      const propertyTypes = data.map((o) => ({
        value: o._id,
        label: o.name,
        ...o,
      }));
      state.propertyTypes = [...propertyTypes];
    },
    setPropertyId: (state = basicSlice?.initialState, action) => {
      state.propertyId = action.payload;
    },
    setPropertyName: (state = basicSlice?.initialState, action) => {
      state.propertyName = action.payload;
    },
    setPropertyStatus: (state = basicSlice?.initialState, action) => {
      state.propertyStatus = action.payload;
    },
    setTotalDraft: (state = basicSlice?.initialState, action) => {
      state.TotalDraft = action.payload;
    },
    setShowPendingProperty: (state = basicSlice?.initialState, action) => {
      state.showPendingProperty = action.payload;
    },
    setBasicDetails: (state = basicSlice?.initialState, action) => {
      state.basicDetails = action.payload;
    },
    setTabStatus: (state = basicSlice?.initialState, action) => {
      state.tabStatus = !state.tabStatus;
    },
    setTabDirty: (state = basicSlice?.initialState, action) => {
      state.tabDirty = action.payload;
    },
    updateShowExtendedColumns: (state, action) => {
      state.showExtendedColumns = action.payload;
    },
    setUnitCreationDraft: (state = basicSlice?.initialState, action) => {
      state.unitCreationDraft = action.payload;
    },
    setPropertyActionStatus: (state = basicSlice?.initialState, action) => {
      state.propertyActionStatus = action.payload;
    },

  },
});
export const {
  updateResidentTypes,
  updatePropertyTypes,
  setPropertyId,
  setBasicDetails,
  setTabStatus,
  setTotalDraft,
  setTabDirty,
  updateShowExtendedColumns,
  setPropertyName,
  setPropertyStatus,
  setPropertyActionStatus,
  setUnitCreationDraft,
  setShowPendingProperty
} = basicSlice.actions;

export default basicSlice.reducer;
