// src/redux/reducers/otherBlocksReducer.js
import { UPDATE_OTHER_BLOCKS_LIST, UPDATE_NUM_ROWS } from "../actions/otherBlocksActions";

const initialState = {
  numRows: 0,
  otherBlocksList: [
    { block: " ", numFloors: "" },
  ],
};

const otherBlocksReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_OTHER_BLOCKS_LIST:
      return {
        ...state,
        otherBlocksList: action.payload,
      };
    case UPDATE_NUM_ROWS:
      return {
        ...state,
        numRows: action.payload,
      };
    default:
      return state;
  }
};

export default otherBlocksReducer;