import {
  INITIATE_INVENTORY_SETUP,
  UPDATE_INVENTORY_VALUE,
} from "../types/index";

const initialState = {
  inventorySetUp: {
    propertyId: "",
    structureType: "FLATWISE", // ROOMWISE, FLATWISE
    rooms: true,
    beds: true,
    isBlocks: true,
    numberOfBlocks: null,
    blockNameFormat: "NUMERIC", //  ALPHANUMERIC, CUSTOM, ALPHABETIC, NUMERIC
    sequenceFrom: "",
    sequenceTo: "",
    buildingName: "",
    isDraft: true,
    shortened: true,
    tabStatus: "PENDING", // PENDING, COMPLETED, INPROGRESS
    floorsPerBlock: [
      {
        block: "",
        numberOfFloors: "",
        shortenedName: "",
        blockName: "",
      },
    ],
    flatsPerFloor: [
      {
        block: "",
        floor: "",
        flats: "",
      },
    ],
  },
};

const unitReducer = (state = initialState, action) => {
  switch (action.type) {
    case INITIATE_INVENTORY_SETUP:
      return {
        ...state,
        inventorySetUp: action.payload,
      };
    case UPDATE_INVENTORY_VALUE:
      return {
        ...state,
        inventorySetUp: {
          ...state.inventorySetUp,
          [action.payload.name]: action.payload.value,
        },
      };

    default:
      return state;
  }
};

export default unitReducer;
