import { combineReducers } from "redux";
import homeReducer from './homeSlice'
import themeSlice from "./themeSlice";
import basicSlice from "./basicSlice";
import blockSequenceReducer from "redux/reducers/blockSequenceReducer";
import otherBlocksReducer from "../reducers/otherBlocksReducer";
import unitReducer from "redux/reducers/unitReducer";

export const reducers = combineReducers({
    home: homeReducer,
    theme: themeSlice,
    basic: basicSlice,
    blockSequence: blockSequenceReducer,
    otherBlocks: otherBlocksReducer,
    unitForm: unitReducer
});


