// src/redux/actions/otherBlocksActions.js

export const UPDATE_OTHER_BLOCKS_LIST = "UPDATE_OTHER_BLOCKS_LIST";
export const UPDATE_NUM_ROWS = "UPDATE_NUM_ROWS";

export const updateOtherBlocksList = (blocks) => {
  return {
    type: UPDATE_OTHER_BLOCKS_LIST,
    payload: blocks,
  };
};

export const updateNumRows = (numRows) => {
    return {
      type: UPDATE_NUM_ROWS,
      payload: numRows,
    };
  };