

export const SET_BLOCK_SEQUENCE_START = "SET_BLOCK_SEQUENCE_START";
export const SET_BLOCK_SEQUENCE_END = "SET_BLOCK_SEQUENCE_END";

export const setBlockSequenceStart = (value) => {
  return {
    type: SET_BLOCK_SEQUENCE_START,
    payload: value,
  };
};

export const setBlockSequenceEnd = (value) => {
  return {
    type: SET_BLOCK_SEQUENCE_END,
    payload: value,
  };
};
